import React from "react";
import styles from "../../css/home/news.module.css";
import { newsAPI } from "../../api/api";

class News extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            news: [],
        };
    }

    async componentDidMount() {
        this.setState({ news: await newsAPI.getNews() })
    }


    render() {

        const newsElements = this.state.news.map((item) => (
            <div className={styles.newsItem}>
                <div className={styles.newsImage}>
                    <img src={item.imagePath} alt="News Image" />
                </div>
                <div className={styles.newsDetails}>
                    <h3 className={styles.newsTitle}>{item.title}</h3>
                    <p className={styles.newsDate}>{item.time}</p>
                    <p className={styles.newsDescription}>{item.body}</p>
                    <a href={item.link} >Read More</a>
                </div>
            </div>
        ))

        return (
            <div className={styles.newsBlock}>
                <h2 className={styles.newsHeading}>Latest News</h2>
                <div className={styles.newsContainer}>
                    {newsElements}
                </div>
            </div>
        );
    }
}

export default News;
